.container {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
}

@media screen and (min-width: 480px) {
    .container {
        margin-right: auto;
        margin-left: auto;
        width: 310px;
    }
}

@media screen and (min-width: 768px) {
    .container {
        width: 734px;
    }
}

@media screen and (min-width: 1366px) {
    .container {
        width: 1678px;
    }
}